<template>
    <v-card flat>
        <v-data-table
        :headers="headers"
        :items="items"
        :loading="loading"
        loading-text="Loading... Please wait"
        :items-per-page="5"
        item-key="name"
        class="elevation-1">
        <template v-slot:item.unique_id="{ item }">
            {{ item.unique_id  }}  |  
            <router-link :to="{ name: 'ViewSale', params: { 'id': item.unique_id }}">
            View Sale <v-icon class="ml-1" color="#E47911" size="16px"> mdi-eye </v-icon>
          </router-link>
        </template>
        <template v-slot:item.amount_sold="{ item }">
            $ {{ item.amount_sold }}
        </template>
        </v-data-table>>
    </v-card>
</template>

<script>
import SalesService from '@/services/SalesService';
import Swal from 'sweetalert2'

export default {
    name: 'Sale',

    data: () => ({
        headers: [
          {
            text: "Sale ID",
            align: 'start',
            value: 'unique_id',
          },
          { text: 'Amount',
            value: 'amount_sold',
          },
        ],
        items: [ ],
        loading: true,
    }),

    computed: {
    idStore(){
      return this.$route.params.id
    }
  },

  methods: {
    refresh_get(){
      SalesService
        .list_sales(this.idStore)
        .then(res => {
          console.log(res)
          this.items = res;
          this.loading = false;
        })
        .catch(e => {
        let error = e.response.data;
        let msg = ''
        Object.entries(error).forEach(([ key, value ]) => {
          msg+= key + ': ' + value + ' \n ';
        });
        this.loading = false,
          Swal.fire({
          icon: 'error',
          title: 'Error',
          text: msg
          });
      })
    },
  },
  
  mounted() {
    this.refresh_get();
  }

}
</script>