<template>
    <v-card flat>
        <v-row class="mt-3 text-right">
            <v-col cols="12">
        <v-btn icon>
            <router-link :to="{ name: 'UpdateWalmart', params:{ 'id': idStore} }">
              <v-icon right color="#E47911"> mdi-pencil </v-icon> </router-link>
        </v-btn>
            </v-col>
        </v-row>
        <v-row class="mb-n8" v-for="(item, id) in options" :key="id">
            <v-col cols="12">
            <h6 class="text-left"> {{ item.text }} </h6>
                <v-text-field
                outlined
                dense
                disabled
                :value="item.value"
                ></v-text-field>
            </v-col>
        </v-row>
    </v-card>
</template>

<script>
  import StoreService from '@/services/StoreService';
  import Swal from 'sweetalert2';

export default {
    name: 'Walmart',

    data: () => ({
        
    options: [
      {
        text: "Walmart username",
        value: ""
      },
      {
        text: "Credit card",
        value: ""
      },
      {
        text: "CVV",
        value: ""
      },
      {
        text: "Expiration date",
        value: ""
      },
    ]
    }),

    computed: {
    idStore(){
      return this.$route.params.id
    }
  },

  methods: {
    refresh_get(){
    StoreService
      .get_walmart(this.idStore)
      .then(res => {
        this.options[0].value = res.walmart_username;
        this.options[1].value = '**** **** **** ' + res.last_4_digits_of_the_card;
        this.options[2].value = res.CVV;
        this.options[3].value = this.expiration_date(res.month, res.year);
      })
      .catch(e => {
      let error = e.response.data;
      let msg = ''
      Object.entries(error).forEach(([ key, value ]) => {
        msg+= key + ': ' + value + ' \n ';
      });
        Swal.fire({
        icon: 'error',
        title: 'Error',
        text: msg
        });
    })
    },

    expiration_date(month, year){
      switch (month) {
        case 1: return 'JAN - ' + year
          
          break;
        
        case 2: return 'FEB - ' + year
          
          break;
        
        case 3: return 'MAR - ' + year
          
          break;
        
        case 4: return 'APR - ' + year
          
          break;
        
        case 5: return 'MAY - ' + year
          
          break;
        
        case 6: return 'JUN - ' + year
          
          break;
        
        case 7: return 'JUL - ' + year
          
          break;
        
        case 8: return 'AUG - ' + year
          
          break;
        
        case 9: return 'SEP - ' + year
          
          break;
        
        case 10: return 'OCT - ' + year
          
          break;
        
        case 11: return 'NOV - ' + year
          
          break;
        
        default: return 'DEC - ' + year
          
          break;
      }
    }
  },

  mounted() {
    this.refresh_get();
  },

}
</script>