<template>
  <v-card flat>
      <v-data-table
      :headers="headers"
      :items="items"
      :loading="loading"
      loading-text="Loading... Please wait"
      :items-per-page="5"
      item-key="name"
      class="elevation-1">
      <template v-slot:item.unique_id="{ item }">
          {{ item.unique_id  }}  |  
          <v-btn plain @click="download_invoice(item.unique_id)">
          View Invoice <v-icon class="ml-1" color="#E47911" size="16px"> mdi-eye </v-icon>
          </v-btn>
      </template>
      <template v-slot:item.status="{ item }">
        <v-chip
        :color="getColor(item.status)"
        dark>
          {{ item.status | name_status }}
        </v-chip>
      </template>
      </v-data-table>>
  </v-card>
</template>

<script>
import InvoiceService from '@/services/InvoiceService';
import Swal from 'sweetalert2'

export default {
    name: 'Invo',

    data: () => ({
        headers: [
          {
            text: "Invoice ID",
            align: 'start',
            value: 'unique_id',
          },
          { text: 'Status',
            value: 'status',
          },
        ],
        items: [ ],
        loading: true,
    }),

    filters:{
      name_status(value){
        if (value == "OK") return 'Paid'
        else if (value == 'P') return 'Pending'
        else return 'Other'
      }
    },
    
    computed: {
    idStore(){
      return this.$route.params.id
    }
  },

  methods: {
    getLink (unique_id) {
      console.log(unique_id)
      return '/viewinvoice'
    },

  getColor (status) {
      if (status == "OK") return 'green'
      else if (status == 'P') return 'orange'
      else return 'red'
    },
    
    refresh_get(){
      InvoiceService
        .list_invoices(this.idStore)
        .then(res => {
          this.items = res;
          this.loading = false;
        })
        .catch(e => {
        let error = e.response.data;
        let msg = ''
        Object.entries(error).forEach(([ key, value ]) => {
          msg+= key + ': ' + value + ' \n ';
        });
        this.loading = false,
          Swal.fire({
          icon: 'error',
          title: 'Error',
          text: msg
          });
      })
    },

    download_invoice(pk){
        Swal.fire({
          icon: 'question',
          title: 'Confirm',
          text: 'Are you sure you want to download this invoice?',
          confirmButtonText: 'YES',
          cancelButtonText: 'NO',
          showCancelButton: true
        }).then(res => {
          if(res.isConfirmed){
            InvoiceService
            .download_invoice(pk)
            .then(res => {
              const url = window.URL.createObjectURL(new Blob([res]));
              const link = document.createElement('a');
              link.href = url;
              link.setAttribute('download', 'Invoice: '+ pk +'.pdf'); //or any other extension
              document.body.appendChild(link);
              link.click();
                Swal.fire({
                  icon: 'success',
                  title: 'Success',
                  text: 'Invoice successfully download.'
                })
            })
            .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
              msg+= key + ': ' + value + ' \n ';
            });
              Swal.fire({
              icon: 'error',
              title: 'Error',
              text: msg
              });
          })
          }
        })
      },
  },
  
  mounted() {
    this.refresh_get();
  }
    
}
</script>