<template>
    <v-card flat>
        <v-row class="mt-3 text-right">
            <v-col cols="12">
        <v-btn icon>
            <router-link :to="{ name: 'UpdateStore', params: { 'id': options[6].value }}">
              <v-icon right color="#E47911"> mdi-pencil </v-icon>
            </router-link>
        </v-btn>
            </v-col>
        </v-row>
        <v-row class="mb-n8" v-for="(item, id) in options" :key="id">
            <v-col cols="12">
            <h6 class="text-left"> {{ item.text }} </h6>
                <v-text-field
                outlined
                dense
                disabled
                :value="item.value"
                ></v-text-field>
            </v-col>
        </v-row>
    </v-card>
</template>

<script>
  import StoreService from '@/services/StoreService';
  import Swal from 'sweetalert2';

export default {
  name: 'BasicInfo',

    data: () => ({
        
    options: [
      {
        text: "Name",
        value: ""
      },
      {
        text: "Phone number",
        value: ""
      },
      {
        text: "Email",
        value: ""
      },
      {
        text: "Address",
        value: ""
      },
      {
        text: "Zipcode",
        value: ""
      },
      {
        text: "State",
        value: ""
      },
      {
        text: "Unique ID",
        value: ""
      },
      {
        text: "Joined on",
        value: ""
      },
    ],
    }),

  computed: {
    idStore(){
      return this.$route.params.id
    }
  },

  methods: {
    refresh_get(){
    StoreService
      .get_info(this.idStore)
      .then(res => {
        this.options[0].value = res.name
        this.options[1].value = res.phone_number
        this.options[2].value = res.email
        this.options[3].value = res.address
        this.options[4].value = res.zipcode
        this.options[5].value = res.state
        this.options[6].value = res.unique_id
        this.options[7].value = res.registered_at
      })
      .catch(e => {
      let error = e.response.data;
      let msg = ''
      Object.entries(error).forEach(([ key, value ]) => {
        msg+= key + ': ' + value + ' \n ';
      });
        Swal.fire({
        icon: 'error',
        title: 'Error',
        text: msg
        });
    })
    }
  },

  mounted() {
    this.refresh_get();
  }
}
</script>