<template>
    <v-card flat>
        <v-data-table
        :headers="headers"
        :items="items"
        :loading="loading"
        loading-text="Loading... Please wait"
        :items-per-page="5"
        item-key="name"
        class="elevation-1">
        <template v-slot:item.owner="{ item }">
            {{ item.owner.first_name + ' ' + item.owner.last_name  }}  | 
          <router-link
            :to="getLink(item.unique_id)"
          >
            View Owner Profile <v-icon class="ml-1" color="#E47911" size="16px"> mdi-eye </v-icon>
          </router-link>
        </template>
        <template v-slot:item.id="{ item }">
          <v-list-item @click="remove_owner(item.id)" link>
              <v-list-item-title> <v-icon left color="#E47911" size="20px"> mdi-window-close </v-icon> Remove owner </v-list-item-title>
            </v-list-item>
        </template>
        </v-data-table>
    </v-card>
</template>

<script>
  import StoreService from '@/services/StoreService';
  import Swal from 'sweetalert2';

export default {
    name: 'Owners',

    data: () => ({
        headers: [
          {
            text: "Owner's name",
            align: 'start',
            value: 'owner',
          },
          { text: 'Actions',
          value: 'id',
          sortable: false,
          },
        ],
        items: [ ],
        loading: true,
    }),

    computed: {
    idStore(){
      return this.$route.params.id
    }
  },

  methods: {
    getLink (unique_id) {
      console.log(unique_id)
      return '/viewownerprofile'
    },

    refresh_get(){
      StoreService
        .list_owners(this.idStore)
        .then(res => {
          this.items = res;
          this.loading=false;
        })
        .catch(e => {
        let error = e.response.data;
        let msg = ''
        Object.entries(error).forEach(([ key, value ]) => {
          msg+= key + ': ' + value + ' \n ';
        });
          Swal.fire({
          icon: 'error',
          title: 'Error',
          text: msg
          });
      })
    },

    remove_owner(id){
      StoreService
        .remove_owner(id)
        .then(() => {
          Swal.fire({
            icon: 'success',
            title: 'Success',
            text: 'The owner has removed successfully.'
          })
          .then(() => this.refresh_get());
        })
        .catch(e => {
          let error = e.response.data;
          let msg = ''
          Object.entries(error).forEach(([ key, value ]) => {
            msg+= key + ': ' + value + ' \n ';
          });
          Swal.fire({
            icon: 'error',
            title: 'Error',
            text: msg
          });
      })

    }
  },

  mounted() {
    this.refresh_get();
  }
    
}
</script>