<template>
  <div>

<nav-bar :name="title"></nav-bar>

    <v-container class="text-center">
      <br>
      <p> {{ data_store.name }} </p>
      <h5 class="text-uoverlinepercase text-center">
        <template>
          <v-chip
            :color="getColor(status)"
            dark
          >
            {{ status }}
          </v-chip>
        </template>
      </h5>
      <br>

    <v-card flat>
      <v-tabs
        v-model="tab"
        background-color="primary"
        dark
      >
        <v-tab> Basic Info </v-tab>
        <v-tab> Owners </v-tab>
        <v-tab> Invoices </v-tab>
        <v-tab> Sales </v-tab>
      </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item>
        <v-row>
          <v-col cols="6">
            <basic-info></basic-info>
          </v-col>
          <v-col cols="6">
            <walmart></walmart>
          </v-col>
        </v-row>
      </v-tab-item>
    
      <v-tab-item>
        <owners></owners>
      </v-tab-item>
      
      <v-tab-item>
        <invo></invo>
      </v-tab-item>
      
      <v-tab-item>
        <sale></sale>
      </v-tab-item>
    </v-tabs-items>
    </v-card>
    
    </v-container>

    <footer-nav></footer-nav>

  </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import FooterNav from '@/components/Footer';
import BasicInfo from '@/components/BasicInfo';
import Owners from '@/components/Owners';
import Invo from '@/components/Invo';
import Sale from '@/components/Sale';
import Walmart from '@/components/Walmart';

  export default {
    components: {
      NavBar, FooterNav, BasicInfo, Walmart, Owners, Invo, Sale
    },

    data: () => ({
      title: 'STORE INFO',
        tab: null,
    }),

    computed:{
      /* SELECCIONA LA PRIMERA TIENDA DEL USUARIO */
        data_store(){
            return this.$store.state.auth.store_user;
        },
      /* CARGA TODAS LAS TIENDAS */
        stores(){
          return this.$store.state.auth.user.stores;
        },

        status(){
            switch(this.data_store.status){
                case 'A' : return 'ACTIVE';
                break;
                case 'S' : return 'SUSPENDED';
                break;
                case 'DA' : return 'DENIED ACTIVATION';
                break;
                case 'V' : return 'VACATION';
                break;
                case 'ED' : return 'EVERYDAY';
                break;
                default : return 'PENDING ACTIVATION';
                break;
            }
        },
        
        idStore(){
          return this.$route.params.id
        }
    },

    methods: {
      getColor (status) {
        if (status == 'ACTIVE') return 'green'
        else if (status == 'EVERYDAY') return 'purple'
        else if (status == 'SUSPENDED') return 'red'
        else if (status == 'DENIED ACTIVATION') return 'gray'
        else if (status == 'VACATION') return 'yellow'
        else return 'orange'
      },
    },
  }
</script>